/** è·å–è”åˆç±»å‹ä¸­æ‰€æœ‰å¯èƒ½çš„é”® */
type AllKeys<T> = T extends any ? keyof T : never;

/** è·å–è”åˆç±»å‹ä¸­æŸä¸ªé”®å¯¹åº”çš„æ‰€æœ‰å¯èƒ½å€¼ç±»å‹ */
type ValueOfKey<T, K extends string> = T extends { [P in K]?: infer V } | { [P in K]: infer V } ? V : never;

